header {
  position: fixed;
  z-index: 20;
  top: 0;
  left: 0;
  width: 100%;
  transition: .2s; }

.header_bg {
  background-color: #23262f;
  transition: .2s;
  box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.09); }

.header,
.header_2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative; }

.header_2 {
  display: none; }

.logo {
  width: 50px;
  margin-right: 10px; }
  .logo a {
    width: 100%; }
    .logo a img {
      width: 100%; }
  .logo_desctop {
    display: block; }
  .logo_mobil {
    display: none; }

.navbar-nav {
  padding: 0;
  display: flex;
  align-items: center; }

.navbar_small {
  padding: 0; }

.nav-item {
  display: flex;
  align-items: center;
  margin-right: 20px; }
  .nav-item a {
    font-size: 16px;
    line-height: 16px;
    display: flex;
    align-items: center;
    color: #adb7d0; }
  .nav-item .icon {
    display: flex;
    align-items: center;
    margin-left: 5px; }
  .nav-item:hover .nav-link {
    color: #f1c86e; }
  .nav-item .active {
    color: #f1c86e;
    cursor: default; }

.nav-link {
  z-index: 2;
  cursor: pointer; }

.dropdown {
  position: relative; }
  .dropdown-menu {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1000;
    float: left;
    min-width: 1rem;
    padding: 0 0;
    margin: 0 0 0;
    font-size: 1rem;
    color: #212529;
    text-align: left;
    list-style: none;
    background-color: #23262f;
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.25);
    background-clip: padding-box;
    visibility: hidden;
    transform: translateY(5px);
    opacity: 0;
    transition: 0.3s;
    z-index: 1; }
    .dropdown-menu.show {
      transition: 0.3s;
      visibility: visible;
      transform: translateY(0px);
      opacity: 1;
      z-index: 3; }
  .dropdown-item {
    display: block;
    width: 100%;
    padding: 0.5rem 1rem;
    clear: both;
    font-weight: 400;
    color: #212529;
    text-align: inherit;
    white-space: nowrap;
    background-color: transparent;
    border: 0;
    transition: 0.3s; }
    .dropdown-item:hover {
      color: #f1c86e;
      transition: 0.3s; }
    .dropdown-item-active {
      color: #f1c86e;
      transition: 0.3s; }

.leng {
  margin-left: auto; }

.shop,
.acounts {
  width: 44px;
  height: 44px;
  background-color: #23262f;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
  cursor: pointer; }
  .shop:hover,
  .acounts:hover {
    transition: 0.3s;
    background-color: #ffffff12; }
  .shop a,
  .acounts a {
    cursor: pointer;
    position: relative; }
    .shop a span,
    .acounts a span {
      position: absolute;
      background: #f1c86e;
      border: 1px solid #adb7d0;
      border-radius: 50%;
      min-width: 18px;
      min-height: 18px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #23262f;
      font-size: 12px;
      right: -40%;
      top: -40%; }
    .shop a img,
    .acounts a img {
      cursor: pointer; }

.search {
  display: flex;
  align-items: center;
  width: 200px;
  position: relative;
  padding: 0; }
  .search_field {
    border-radius: 21px;
    text-indent: 1rem;
    padding: 10px 0;
    border: none;
    outline: none;
    position: absolute;
    z-index: 2;
    right: 15px;
    width: 10px;
    transition: 0.3s;
    color: #23262f; }
  .search_icon {
    margin-left: auto;
    width: 44px;
    height: 44px;
    background-color: #23262f;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.3s;
    cursor: pointer;
    z-index: 4; }
    .search_icon svg {
      cursor: pointer; }
    .search_icon img {
      cursor: pointer; }
    .search_icon:hover {
      transition: 0.3s;
      background-color: #33353e; }
  .search.active {
    transition: 0.3s; }
    .search.active .search_field {
      transition: 0.3s;
      width: calc(100% - 15px); }
    .search.active .search_icon {
      transition: 0.3s;
      background-color: #33353e;
      box-shadow: -3px 0px 15px rgba(0, 0, 0, 0.5); }

.search_mobil {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  position: relative; }
  .search_mobil__box {
    width: 100%; }
  .search_mobil input {
    outline: none;
    border: none;
    background: none;
    padding: 17px 0;
    text-indent: 30px;
    color: #fff; }
  .search_mobil .icon {
    position: absolute;
    right: 0;
    height: 100%;
    width: 55px;
    background: #23262f;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center; }

.acounts {
  position: relative; }
  .acounts_form {
    position: absolute;
    position: absolute;
    top: 39px;
    z-index: 10;
    padding: 30px;
    background: #23262f;
    min-width: 320px;
    right: 50%;
    transform: translate(34%, -4px);
    z-index: -10;
    opacity: 0;
    visibility: hidden;
    transition: 0.3s; }
    .acounts_form.active {
      box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.25);
      transition: 0.3s;
      transform: translate(34%, 0px);
      z-index: 25;
      opacity: 1;
      visibility: visible; }
  .acounts_modal__header {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px; }
    .acounts_modal__header img {
      margin-right: 10px; }
    .acounts_modal__header div {
      font-size: 16px;
      line-height: 16px;
      display: flex;
      align-items: center;
      color: #adb7d0; }
  .acounts_modal__main {
    margin: 20px 0 0; }
  .acounts_modal__footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px; }

main {
  margin-top: 86px; }

@media (max-width: 1290px) {
  header {
    background-color: #23262f;
    transition: .2s;
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.09); }
  .slider_hero {
    margin-top: -30px !important; } }

@media (max-width: 1105px) {
  .nav-item {
    margin-right: 15px; } }

@media (max-width: 1050px) {
  main {
    margin-top: 130px; }
  .logo {
    width: 112px; }
    .logo_desctop {
      display: none; }
    .logo_mobil {
