.about {
  display: grid;
  grid-template-columns: 1fr 30%;
  grid-gap: 90px;
  align-items: center;
  position: relative;
  border-bottom: 1px solid rgba(173, 183, 208, 0.2);
  overflow: hidden; }
  .about_info {
    max-width: 550px;
    display: flex;
    flex-direction: column;
    align-items: center; }
    .about_info .main {
      display: grid;
      margin-right: -110px;
      grid-template-columns: 160px 1fr;
      margin-right: -110px; }
      .about_info .main .logo {
        margin-right: 10px;
        width: 150px;
        grid-row-start: 1;
        grid-row-end: 3; }
        .about_info .main .logo img {
          width: 100%; }
      .about_info .main .info .title {
        letter-spacing: 0.255em;
        text-transform: uppercase;
        color: #ffffff; }
      .about_info .main .info .subtitle {
        font-weight: 300;
        font-size: 16px;
        line-height: 28px;
        color: #ffffff;
        opacity: 0.5; }
      .about_info .main .year {
        display: flex;
        align-items: center;
        letter-spacing: 0.25em;
        text-transform: uppercase;
        color: #adb7d0; }
        .about_info .main .year span {
          text-transform: uppercase;
          font-size: 150px; }
    .about_info .footer {
      font-weight: 300;
      font-size: 18px;
      line-height: 20px;
      color: #adb7d0;
      max-width: 540px; }
      .about_info .footer span {
        font-weight: 500; }
  .about_partners ul li {
    height: 51px;
    margin-bottom: 35px; }
    .about_partners ul li img {
      height: 100%; }
    .about_partners ul li:last-child {
      margin-bottom: 20px; }
  .about_bg {
    pointer-events: none;
    position: absolute;
    height: 140%;
    height: 140%;
    width: 115%;
    bottom: -2px;
    left: 50%;
    transform: translate(-50%, 0); }
    .about_bg_img {
      height: 100%;
      width: 100%;
      object-fit: contain; }
  .about_footer {
    font-weight: 300;
    font-size: 25px;
    line-height: 35px;
    color: #7e8499;
    margin: 50px 0; }
    .about_footer span {
      font-weight: 500; }

.description_wraper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 40px;
  align-items: center;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(173, 183, 208, 0.2); }

.description_info {
  position: relative;
  margin-top: 50px; }
  .description_info .subtitle {
    font-weight: 300;
    font-size: 20px;
    line-height: 43px;
    color: #adb7d0; }
  .description_info .title {
    font-size: 40px;
    line-height: 43px;
    text-transform: uppercase;
    color: #ffffff;
    font-weight: 300; }
    .description_info .title span {
      font-weight: 500; }
  .description_info_box {
    background: #23262f;
    box-shadow: 20px 20px 50px rgba(0, 0, 0, 0.25);
    padding: 14px 32px 0;
    width: 120%;
    margin: 30px 0;
    position: relative; }
    .description_info_box p {
      font-style: normal;
      font-weight: 300;
      font-size: 16px;
      line-height: 28px;
      color: #f1c86e;
      width: 70%;
      margin: 0; }
    .description_info_box .btn {
      transform: translate(0, 50%);
      margin-left: 30%; }
  .description_info_img {
    position: absolute;
    top: 50%;
    transform: translate(0%, -50%);
    right: 0%;
    height: 275%;
    width: auto; }
    .description_info_img img {
      height: 100%;
      width: auto; }

.description_category .categories {
  display: grid;
  place-items: center;
  grid-template-columns: repeat(auto-fill, minmax(210px, 1fr));
  overflow: auto;
  grid-gap: 30px; }

.description_category .categorie {
  display: grid;
  place-items: center;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  overflow: auto; }
  .description_category .categorie__item {
    position: relative;
    background: #23262f; }
    .description_category .categorie__item::after {
      content: "";
      border: 1px solid #adb7d0;
      box-sizing: border-box;
      position: absolute;
      opacity: 0.2;
      top: 50%;
      left: 50%;
      height: 100%;
      width: 100%;
      transform: translate(-50%, -50%);
      transition: 0.3s; }
    .description_category .categorie__item img {
      display: block;
      height: 100%;
      width: 100%;
      object-fit: cover; }
    .description_category .categorie__item_text {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      color: #adb7d0;
      font-size: 16px;
      line-height: 29px;
      font-weight: 300;
      text-transform: uppercase;
      text-align: center;
      transition: 0.3s; }
      .description_category .categorie__item_text span {
        font-weight: 500; }
    .description_category .categorie__item:hover:not(.active)::after {
      background: #f1c86e;
      opacity: 1;
      opacity: 0.8;
      transition: 0.3s;
      z-index: 1; }
    .description_category .categorie__item:hover:not(.active) .categorie__item_text {
      cursor: pointer;
      transition: 0.3s;
      color: #23262f;
      opacity: 1;
      z-index: 2; }
      .description_category .categorie__item:hover:not(.active) .categorie__item_text span {
        cursor: pointer; }

@media (max-width: 1200px) {
  .description_info_img {
    transform: translate(-10%, -50%);
    height: 200%;
    top: 40%;
    right: -2%; }
  .about_partners ul {
    padding: 0; } }

@media (max-width: 1100px) {
  .description_info_img {
    top: 32%;
    right: -6%;
    height: 185%; } }

@media (max-width: 992px) {
  .about {
    grid-template-columns: 1fr;
    grid-gap: 0px; }
    .about_bg {
      height: 100%;
      width: 100%;
      top: -140px;
      bottom: unset; }
    .about_info {
      max-width: 100%; }
      .about_info .main {
        margin: 0; }
    .about_partners {
      width: 90%;
      margin: 40px auto 0; }
      .about_partners ul {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        grid-column-gap: 20px;
        padding: 0; }
        .about_partners ul li {
          height: 41px;
          margin-bottom: 25px; }
  .description_wraper {
    grid-template-columns: 1fr;
    grid-gap: 10px; }
  .description_category .categories {
    grid-gap: 15px !important; }
  .description_info_box {
    width: 100%; }
  .description_info_img {
    top: 50%;
    right: -2%;
    height: 253%; } }

@media (max-width: 892px) {
  .description_info_img {
    top: 50%;
    right: -2%;
    height: 233%; } }

@media (max-width: 850px) {
  .categories .categorie__item {
    grid-gap: 15px !important;
    width: 100%;
    height: 90px; } }

@media (max-width: 768px) {
  .description {
    display: grid;
    grid-template-columns: 1fr; }
    .description_info_img {
      display: none; }
    .description_info_box p {
      width: 100%; }
    .description_info .title {
      font-size: 30px; }
    .description_img {
      display: none; }
    .description_category .categories {
      grid-template-columns: 1fr; }
      .description_category .categories .categorie__item {
        height: 120px; }
  .about_wraper {
    padding: 0 !important; }
    .about_wraper .about_info, .about_wraper .about_partners, .about_wraper .about_footer {
      width: 90%;
      margin: 0 auto; }
  .about_info .main {
    margin: 0; }
  .about_bg {
    width: 110%;
    height: auto;
    top: -100px;
    left: 45%; }
  .about_footer {
    font-size: 18px;
    line-height: 1.55;
    color: #7e8499;
    margin: 10px auto !important; } }

@media (max-width: 560px) {
  .description_categories .categories {
    grid-template-columns: 1fr; }
    .description_categories .categories .categorie__item {
      height: 90px; }
  .description_info_box .btn {
    width: fit-content;
    margin: 0 auto;
    display: block; }
  .about_info .main {
    width: 80%; }
    .about_info .main .logo {
      grid-row-start: 2;
      grid-row-end: 3; }
    .about_info .main .year {
      grid-column-start: 2;
      grid-column-end: 4;
      grid-row-start: 2;
      grid-row-end: 3;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column; }
      .about_info .main .year span {
        line-height: .8; }
    .about_info .main .info {
      grid-column-start: 1;
      grid-column-end: 4; }
  .about_bg {
    width: 110%;
    height: auto;
    top: -10%;
    left: 45%; } }

@media (max-width: 480px) {
  .about_info .main {
    width: 100%; }
  .about_partners ul {
    flex-direction: column;
    align-items: baseline; }
    .about_partners ul li {
      padding-left: 30px;
      height: 41px;
      margin-bottom: 25px;
      position: relative; }
      .about_partners ul li:before {
        content: '';
        display: block;
        height: 10px;
        width: 10px;
        background: #ADB7D0;
        position: absolute;
        left: 0px;
        top: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%; } }
